export default{
	onLoad: (e) => {
		this.shopid = e.shopid
		this.phone = uni.getStorageSync('phone')
	},
	data(){
		return{
			phone:1,
			shopid:''
		}
	},
	methods:{
		toMore(e){
			uni.navigateTo({
				url:"../userDetails/userDetails?userid="+e
			})
		}
		
	}
}
